import React from 'react'

const Toolbar = ({ fontSize, setFontSize, contrast, setContrast, languages, lang, setLang }) => {

    return (
        <React.Fragment>
            <div className={`app-toolbar${(contrast ? ' app-toolbar--contrast' : '')}`} >
                <div className="app-toolbar--left">
                    <div className="app-toolbar--item" onClick={() => setFontSize(Math.max(fontSize - 10, 50))}>
                        <span>A-</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setFontSize(100)}>
                        <span>A</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setFontSize(Math.min(fontSize + 10, 150))}>
                        <span>A+</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setContrast(!contrast)}>
                        <span>Wersja kontrastowa</span>
                    </div>
                </div>
                <div className="app-toolbar--right">
                    <div className="app-toolbar--item">
                        <div className="app-toolbar--language-choosed">
                            <i className={`flag:${lang}`}></i>
                        </div>
                        <div className="app-toolbar--language-list">
                            {
                                languages.map((language) =>
                                    <i key={language.short} className={`app-toolbar--language flag:${language.short}`} title={language.name} onClick={() => setLang(language.short)}></i>
                                )
                            }
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar